// https://observablehq.com/@robinl/gatsby-test-2@14
export default function define(runtime, observer) {
  const main = runtime.module();
  main.variable(observer("first_cell")).define("first_cell", ["md"], function(md){return(
md`# Gatsby test named cells`
)});
  main.variable(observer("third_cell")).define("third_cell", ["md"], function(md){return(
md`hello third cell`
)});
  main.variable(observer("second_cell")).define("second_cell", ["md"], function(md){return(
md`**second cell**`
)});
  return main;
}
